.showYZM_div,
.b3 {
  display: flex;
  align-items: center;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-sizing: border-box;
  padding-left: 15px;
  margin-bottom: 20px;
}
.biao img {
  width: 25px;
  height: 25px;
}
.biao {
  text-align: center;
  margin-left: 25px;
  margin-right: 25px;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
}
.biaoz {
  margin-bottom: 20px;
  background: #8bbd6f;
}
.biaox {
  text-align: center;
  font-size: 15px;
  margin-bottom: 45px;
  color: darkgrey;
}
.biaox a {
  text-decoration: none;
  color: #8bbd6f;
}
.biaoy {
  margin-bottom: 120px;
  color: darkgrey;
}
.biaoy a {
  text-decoration: none;
  color: #8bbd6f;
}
.biaozd {
  color: darkgray;
}
.a3 a {
  text-decoration: none;
  color: black;
  margin-top: 40px;
}
.footer p {
  color: #aaa;
  margin-bottom: 10px;
}
/*  */
.yzm_div{
  border:none;
}
.yzm_text_div{
  width: 90px;
  margin-right: 5px;
}
.yzm_text_div a{
  color:#368E05;
  margin-left: 5px;
}
.yzm_picture_div{
  width: 76px;
